import coupon from '@/components/couponCom/coupon/coupon.vue';
import mIcon from "@/components/m-icon/m-icon.vue"
import {CouponSelectState,CouponType} from "./couponSelectType"
@Component({components:{mIcon,coupon}})
export default class CouponSelect extends Vue implements CouponSelectState {
    @Prop()
    coupons:Array<CouponType>
    @Prop()
    visible:boolean
     ycoupons=[]
    noCoupons=[]
    hasClick=true

    @Watch("coupons",{
      immediate:true
    })
    couponChange() {
    const useList = [];
    const noUseList = [];
    const coupons = this.coupons;
    coupons.forEach(item => {
      item.isChoose = false;
      if (item.canUse) {
        useList.push(item);
      }
      if (!item.canUse) {
        noUseList.push(item);
      }
    });
    this.ycoupons=useList
    this.noCoupons=noUseList
    }
      /** 触发外部关闭事件 */
  onClose() {
    this.$emit("onClose", {}, {});
  }
  /** 顶部选择查看优惠券 */
  chooseWay() {
    this.setData({
      hasClick: !this.hasClick,
    });
  }

  /**
   * 选择要使用的优惠券
   */
  chooseCoupon(item) {
    const cItem = item;
    const useList = this.ycoupons as any;
    useList.forEach(useItem => {
      if (useItem.id === cItem.id) {
        useItem.isChoose = !useItem.isChoose;
      } else {
        useItem.isChoose = false;
      }
    });
    this.setData({
      [`ycoupons`]: [...useList],
    });
  }

  /** 选择优惠券事件 */
  onTap() {
    const useList = this.ycoupons;
    const item = useList.filter(v => {
      return v.isChoose === true;
    });
    if (item.length) {
      this.$emit("onSelect", item[0], {});
    } else {
      this.$emit("onSelect", {}, {});
    }
  }
}
